import foodCard from '../public/foodCard.vue';
export default {
  components: { foodCard },
    data() {
        return {
            user_productions: ''
        }
    },
    props: {
        userId: {
            type: String
        }
    },
    methods: {
        async getUserProductions() {
            const params = {
                user_id : this.userId
            }
            const res = await this.$axios.get('/api/foods',{params});
            this.user_productions = res.data;
            console.log(this.user_productions)
        }
    },
    mounted () {
        this.getUserProductions();
    },
}
   
